import styles from "../../../styles/layout/layout.module.css";
import Link from "next/link";
import {useEffect, useState} from "react";
import {useRouter} from "next/router";
import LoginModal from "../../userService/LoginModal";
import FriendActionModal from "@/component/userService/FriendActionModal";
import ChatSidebar from "@/component/layout/Bar/ChatSidebar";

export default function Topbar() {
  const router = useRouter();
  const [isLoggedIn, setIsLoggedIn] = useState(false);
  const [showLoginModal, setShowLoginModal] = useState(false);
  const [showFriendModal, setShowFriendModal] = useState(false);
  const [isChatOpen, setIsChatOpen] = useState(false);

  // 초기 로그인 상태 확인
  useEffect(() => {
    checkLoginStatus();
  }, []);

  const checkLoginStatus = () => {
    const token = localStorage.getItem("accessToken");
    setIsLoggedIn(!!token);
  };

  const handleLoginSuccess = () => {
    checkLoginStatus();
    setShowLoginModal(false); // 로그인 성공 시 모달 닫기
  };

  const handleLogout = () => {
    localStorage.clear();
    setIsLoggedIn(false);
    alert("로그아웃 되었습니다.");
    router.push("/");
  };

  // 로그인한 사용자 signId/userName 가져오기
  const currentUserSignId = typeof window !== "undefined"
      ? localStorage.getItem("userSignId") || ""
      : "";
  const currentUserName = typeof window !== "undefined"
      ? localStorage.getItem("userName") || "나"
      : "나";


  return (
      <>
        <header className={styles.topbar}>
          <Link href="/" className={styles.leftSection}>
            MomenTory
          </Link>

          <nav className={styles.rightSection}>
            <div className={styles.rightItem}>검색</div>

            {isLoggedIn ? (
                <>
                  <Link href="/community" className={`${styles.rightItem} ${styles.navLink}`}>
                    커뮤니티
                  </Link>
                  <div
                      className={styles.rightItem}
                      style={{cursor: 'pointer'}}
                      onClick={() => setShowFriendModal(true)}
                  >
                    친구
                    {/* 💡 개선: FriendActionModal은 최상위(portal)에서 렌더링되도록 구현하는 것이 일반적입니다. */}
                    {/* 렌더링 위치는 그대로 두고, onCloce prop을 제대로 사용하도록 수정했습니다. */}
                    {showFriendModal && currentUserSignId && (
                        <FriendActionModal
                            currentUserSignId={currentUserSignId}
                            isOpen={showFriendModal}
                            // ✅ 수정: 닫을 때는 false를 설정합니다.
                            onClose={() => setShowFriendModal(false)}
                        />
                    )}
                  </div>
                  <div className={styles.rightItem}>알림</div>
                  {/* ✅ 수정: Link 대신 클릭 핸들러를 사용하여 ChatSidebar를 엽니다. */}
                  <div
                      className={`${styles.rightItem} ${styles.writeButton}`}
                      style={{cursor: 'pointer'}}
                      onClick={() => setIsChatOpen(true)}
                  >
                    채팅
                  </div>
                  <Link href="/write" className={`${styles.rightItem} ${styles.writeButton}`}>
                    Log 작성
                  </Link>
                  <div className={styles.rightItem} onClick={handleLogout}
                       style={{cursor: 'pointer'}}>
                    로그아웃
                  </div>
                </>
            ) : (
                <div
                    className={styles.rightItem}
                    style={{cursor: "pointer"}}
                    // 🚨 핵심 수정: 클릭 시 로그인 모달을 열도록 true로 변경합니다.
                    onClick={() => setShowLoginModal(true)}
                >
                  로그인
                </div>
            )}
          </nav>
        </header>

        {showLoginModal && (
            <LoginModal
                onClose={() => setShowLoginModal(false)}
                onLoginSuccess={handleLoginSuccess}
            />
        )}

        {isChatOpen && currentUserSignId && (
            <ChatSidebar
                currentUserId={currentUserSignId}
                currentUserName={currentUserName} // 💡 추가: userName도 사용합니다.
                onClose={() => setIsChatOpen(false)}
            />
        )}
      </>
  );
}